<template lang="pug">
  ul.list
    li.list__icon(v-for="icon in icons" v-in-viewport="true")
      svg.svgicon
        use(xmlns:xlink="http://www.w3.org/1999/xlink" :xlink:href="'#' + icon.name")
      figcaption {{ icon.text }}
</template>


<script>
  export default {
    name: 'Techtransforma',

    data: () => ({
      icons: [
        { name: 'design', text: 'I create responsive websites that work on a wide range of devices' }
      ]
    })
  }
</script>


<style lang="sass" scoped>
  .list
    display: flex
    justify-content: space-between
    +unselectable
    &__icon
      width: 20rem
      margin: 0
      padding-bottom: 40px
      text-align: center
      opacity: 0
      +breakpoint(sm)
        padding-bottom: 40px

      // Animation
      @for $i from 1 through 1
        &:nth-of-type(#{$i})
          &.in-viewport
            animation: (0.5s + ($i - 1) * 0.1s) fadeInUp2 (10ms + ($i - 1) * 0.1s) both

  svg
    width: 90px
    height: 90px
</style>



