<template lang="pug">
  //- <div class="container">
  //-   <div class="photos">
  //-     <img src="http://fakeimg.pl/1366x768/000/?text=1">
  //-     <img src="http://fakeimg.pl/1366x768/111/?text=2">
  //-     <img src="https://hateplanet.es/static/social.png">
  //-     <img src="http://fakeimg.pl/1366x768/333/?text=4">
  //-     <img src="http://fakeimg.pl/1366x768/444/?text=5">
  //-     <img src="http://fakeimg.pl/1366x768/555/?text=6">
  //-   </div>
  //- </div>
  aside
    .spinnerCOMINGSOON
</template>

<script>
export default {
  name: 'Projects'
}
</script>

<style lang="sass" scoped>
  aside
    height: 50vh
    display: flex
    justify-content: center
    align-items: center
    h4
      font-size: 18px
      color: #999
</style>

<style>
.container {
  width: 100%;
  max-width: 1680px;
  margin: 2.5em auto;
}

.photos {
  display: flex;
  background-color: red;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-content: stretch;
  padding: 0;
  max-width: 100%;
}

.photos img {
  display: block;
  flex: 0 0 auto;
  background-color: #fff;
}


@media screen and (min-width: 1024px) {
  .container {
    margin-top: 4em;
  }
  .photos img {
    width: calc(100%/3);
    height: calc(100%/3);
  }
}

@media screen and (min-width: 769px) and (max-width: 1024px) {
  .photos img {
    width: calc(100%/4);
    height: calc(100%/4);
  }
}

@media screen and (min-width: 481px) and (max-width: 768px) {
  .photos img {
    width: calc(100%/3);
    height: calc(100%/3);
  }
}

@media screen and (min-width: 321px) and (max-width: 480px) {
  .photos img {
    width: calc(100%/2);
    height: calc(100%/2);
  }
}

@media screen  and (max-width: 320px) {
  .photos img {
    width: 100%;
    height: 100%;
  }
}





.spinnerCOMINGSOON {
  width: 40px;
  height: 40px;
  margin: 100px auto;
  background-color: #af9fda;

  border-radius: 100%;
  -webkit-animation: sk-scaleout 1.0s infinite ease-in-out;
  animation: sk-scaleout 1.0s infinite ease-in-out;
}

@-webkit-keyframes sk-scaleout {
  0% { -webkit-transform: scale(0) }
  100% {
    -webkit-transform: scale(1.0);
    opacity: 0;
  }
}

@keyframes sk-scaleout {
  0% {
    -webkit-transform: scale(0);
    transform: scale(0);
  } 100% {
    -webkit-transform: scale(1.0);
    transform: scale(1.0);
    opacity: 0;
  }
}
</style>



