<template lang="pug">
  section#Tres

    SectionTitle(color="#F2E059" seo="03. Some latest projects I've been doing")
      span(slot="number") 3
      span(slot="text1") Portfolio
      span(slot="text2") LATEST PROJECTS


    blockquote.slogan(v-in-viewport.once="true") Sorry, I will update the showcase soon! <div class="brumm">🚀</div>

    Projects
</template>



<script>
  import Projects from '@/components/Projects'

  export default {
    components: { Projects }
  }
</script>



<style lang="sass" scoped>
#Tres
  min-height: 100vh

  blockquote
    color: #efefef
    text-align: left
    line-height: 1.3
    font-weight: 200
    font-size: calc(1.25rem + (3 - 2) * (100vw - 32rem)/ (76.8 - 32))
    margin: calc(4rem + (6 - 4) * (100vw - 32rem)/ (76.8 - 32)) 0
    opacity: 0
    &.in-viewport
      animation: .550s fadeIn 1250ms both
    +breakpoint(md)
      font-size: calc(1.5rem + (3 - 2) * (100vw - 32rem)/ (76.8 - 32))
      // margin-left: 15%

    &.slogan
      color: #fff
      font-weight: bold
      &.in-viewport
        animation: 1.25s fadeInUp2 550ms both


    .svgicon
      width: 24px
      height: 24px

  .brumm
    display: inline-block
    animation: brumm 340ms
    animation-fill-mode: forwards
    animation-play-state: running
    animation-iteration-count: infinite

  @keyframes brumm
    0%
      transform: translateY(0)
    50%
      transform: translateY(-4px)
    100%
      transform: translateY(0)
</style>



